}
.directorist-tooltip.directorist-tooltip-secondary[aria-label]:after {
  background-color: #122069;
}
.directorist-tooltip.directorist-tooltip-secondary[aria-label]:before {
  border-bottom-color: #122069;
}
.directorist-tooltip.directorist-tooltip-info[aria-label]:after {
  background-color: #2C99FF;
}
.directorist-tooltip.directorist-tooltip-info[aria-label]:before {
  border-top-color: #2C99FF;
}
.directorist-tooltip.directorist-tooltip-warning[aria-label]:after {
  background-color: #EF8000;
}
.directorist-tooltip.directorist-tooltip-warning[aria-label]:before {
  border-top-color: #EF8000;
}
.directorist-tooltip.directorist-tooltip-success[aria-label]:after {
  background-color: #00AC17;
}
.directorist-tooltip.directorist-tooltip-success[aria-label]:before {
  border-top-color: #00AC17;
}
.directorist-tooltip.directorist-tooltip-danger[aria-label]:after {
  background-color: #EF0000;
}
.directorist-tooltip.directorist-tooltip-danger[aria-label]:before {
  border-top-color: #EF0000;
}
.directorist-tooltip.directorist-tooltip-bottom.directorist-tooltip-primary[aria-label]:before {
  border-bottom-color: #444752;
}
.directorist-tooltip.directorist-tooltip-bottom.directorist-tooltip-secondary[aria-label]:before {
  border-bottom-color: #122069;
}
.directorist-tooltip.directorist-tooltip-bottom.directorist-tooltip-info[aria-label]:before {
  border-bottom-color: #2C99FF;
}
.directorist-tooltip.directorist-tooltip-bottom.directorist-tooltip-warning[aria-label]:before {
  border-bottom-color: #EF8000;
}
.directorist-tooltip.directorist-tooltip-bottom.directorist-tooltip-success[aria-label]:before {
  border-bottom-color: #00AC17;
}
.directorist-tooltip.directorist-tooltip-bottom.directorist-tooltip-danger[aria-label]:before {
